function compressImage (options) {
  const img = document.createElement('img');
  const format = options.format;

  img.src = options.src
  img.onload = () => {
    let mimeType

    if (/image\//.test(format)) {
      mimeType = format
    } else {
      switch (format) {
        case 'png':
          mimeType = 'image/png'
          break
        case 'webp':
          mimeType = 'image/webp'
          break
        default:
          // jpg, jpeg, etc..
          mimeType = 'image/jpeg'
      }
    }

    const canvas = document.createElement('canvas')
    const adjustedSize = adjustSize(img.naturalWidth, img.naturalHeight, (options.maxSize || 1920))
    canvas.width = adjustedSize.width
    canvas.height = adjustedSize.height
    // canvas.width = img.naturalWidth
    // canvas.height = img.naturalHeight
    canvas
      .getContext("2d")
      .drawImage(img, 0, 0, adjustedSize.width, adjustedSize.height)

    callback({
      origin: canvas.toDataURL(mimeType, options.quality / 100),
      thumbnail: makeThumbnail(img, mimeType, options)
    })
  }
}


let opts = {};

function imazip(options) {
  return new Promise(async (resolve, reject) => {
    opts = options;
    resolve(opts.width + 200);
  });
}

export default imazip;
